import React, { Component } from 'react'
import QrReader from 'react-qr-reader'
 
class Additem extends Component {
  state = {
    qrid: []
  }
 
  handleScan = data => {
    if (this.state.qrid.indexOf(data)===-1) {

      this.setState(state => {
        const qrid = this.state.qrid.concat(state.value);
  
        return {
          qrid,
          value: data,
        };
      });

      console.log(this.state.qrid);

    }
  }





  handleError = err => {
    console.error(err)
  }
  render() {
    return (
      <div className='container'>
        <br/>
        <center>
          <h5>Scan a product's QR code to add it to your cart.</h5>
          <QrReader
          delay={300}
          onError={this.handleError}
          onScan={this.handleScan}
          style={{ width: '60%' }}
        /></center>
        <p>{this.state.qrid}</p>
      </div>
    )
  }
}

export default Additem;