import React, { Component } from 'react';

export default class Login extends Component{
    state = {
        email : null,
        password : null
    }

    handleChange = (e) => {
        const submitButton = document.querySelector('#signupButton');

        this.setState({
            [e.target.id] : e.target.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        
        return fetch("https://cors-anywhere.herokuapp.com/http://54.189.131.192:8192/signin", {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: "same-origin", // include, *same-origin, omit
            headers: {'Content-Type': 'application/json', 'Accept': 'application/json', 'Access-Control-Allow-Headers': 'application/json' },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(this.state), // body data type must match "Content-Type" header
        })
        .then(response => response.json())
        .then(
            data => { 
                console.log(data);
                // Store data
                var userID = JSON.stringify(data);
                localStorage.setItem('QSuserID', userID);
                window.location.href = "/";
            })
        .catch(error => console.log("Captured an error: " + error));
    }

    render(){

        return(
            <div className='container'>
                <h3>Sign in to your account</h3>
                <p id="alert"></p>
                <form onSubmit={this.handleSubmit}>
                    <label htmlFor="email">Email:</label>
                    <input type='text' id="email" onChange={this.handleChange}/>
                    <label htmlFor="password">Password:</label>
                    <input type='password' id="password" onChange={this.handleChange}/>
                    <button id='signupButton' type="submit">Sign in</button>                    
                </form>

                <div id='qrCode'></div>
            </div>
        )
    }
}