import React from 'react';
import QRCode from 'qrcode.react';

const GenQR = () => {
    const userID = localStorage.getItem('QSuserID');
    if(userID && userID!==false){

        return (
            <div>
                <center><QRCode
                    id="QrCode"
                    value={userID}
                    size={150}
                    level={"H"}
                    includeMargin={true}
                /></center>
            </div>
        )

    }

}

export default GenQR;