import React from 'react'

const Navbar = () => {

    const userID = localStorage.getItem('QSuserID');
    if(userID && userID!==false){
        return(
            <div>
                <nav className="z-depth-0">
                    <div className="nav-wrapper container">
                    <a href="/">Quick<span>Shop</span></a>
                    <span className="right grey-text text-darken-1">
                        <i className="material-icons sidenav-trigger" data-target="side-menu">menu</i>
                    </span>
                    </div>
                </nav>
    
                <ul id="side-menu" className="sidenav side-menu">
                    <li><a className="subheader">Quick Shop</a></li>
                    <li><a href="/" className="waves-effect">Home</a></li>
                    <li><a href="/explore" className="waves-effect">Explore store</a></li>
                    <li><a href="/additem" className="waves-effect">Scan to buy items</a></li>
                    <li><a href="/profile" className="waves-effect">Profile</a></li>
                    <li><a href="/" className="waves-effect" onClick={() => { localStorage.removeItem('QSuserID'); }}>Log out</a></li>
                    <li><div className="divider"></div></li>
                    <li><a href="/aboutus" className="waves-effect">About us</a></li>
                    <li><a href="/contactus" className="waves-effect">Contact us</a></li>
                    
                </ul>
            </div>
        )
    }
    else{
        return(
            <div>
                <nav className="z-depth-0">
                    <div className="nav-wrapper container">
                    <a href="/">Quick<span>Shop</span></a>
                    <span className="right grey-text text-darken-1">
                        <i className="material-icons sidenav-trigger" data-target="side-menu">menu</i>
                    </span>
                    </div>
                </nav>
    
                <ul id="side-menu" className="sidenav side-menu">
                    <li><a className="subheader">Quick Shop</a></li>
                    <li><a href="/" className="waves-effect">Home</a></li>
                    <li><a href="/explore" className="waves-effect">Explore store</a></li>
                    <li><div className="divider"></div></li>
                    <li><a href="/aboutus" className="waves-effect">About us</a></li>
                    <li><a href="/contactus" className="waves-effect">Contact us</a></li>
                    
                </ul>
            </div>
        )
    }

}

export default Navbar