import React, { Component } from 'react';
import Select from 'react-select';

const genderOption = [{label: 'Male', value:'Male'}, {label: 'Female', value:'Female'}];




export default class Signup extends Component{
    state = {
            "first_name": null,
            "last_name": null,
            "age": 0,
            "gender": null,
            "email": null,
            "password": null
            }
      

    handleSelect = (e) => {
            this.setState({gender: e.value})
    }
    
    handleChange = (e) => {
        
        const alrt = document.querySelector('#alert');
        const submitButton = document.querySelector('#signupButton');

        if ([e.target.id]!="confirm_password") {
            this.setState({
            [e.target.id] : e.target.value
            })
        }

        if ([e.target.id]=="age") {
            this.setState({
            [e.target.id] : parseInt(e.target.value, 10)
            })
        }

        if ([e.target.id]=="confirm_password") {
            if (this.state.password != e.target.value){
                alrt.innerHTML = "Passwords do not match, please make sure you typed it correctly";
                submitButton.disabled = true;
            }
            if (this.state.password == e.target.value){
                alrt.innerHTML = '';
                submitButton.disabled = false;
            }
        }


    }

    handleSubmit = (e) => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        
        return fetch("https://cors-anywhere.herokuapp.com/http://54.189.131.192:8192/signup", {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: "same-origin", // include, *same-origin, omit
            headers: {'Content-Type': 'application/json', 'Accept': 'application/json', 'Access-Control-Allow-Headers': 'application/json' },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(this.state), // body data type must match "Content-Type" header
        })
        .then(response => response.json()).then(data => { console.log(data); window.location.href = "/login"}).catch(error => console.log("Captured an error: " + error));
    }



    render(){

        return(
            <div className='container'>
                <h3>Signup to create an account</h3>
                <p id="alert"></p>
                <form className='signupForm' onSubmit={this.handleSubmit}>
                    <label htmlFor="first_name">First Name:</label>
                    <input type="text" id="first_name" onChange={this.handleChange}/>
                    <label htmlFor="last_name">Last Name:</label>
                    <input type="text" id="last_name" onChange={this.handleChange}/>
                    <label htmlFor="age">Age:</label>
                    <input type='number' id="age" onChange={this.handleChange}/>
                    <label htmlFor="gender">Gender:</label>
                    <Select id='gender' options={genderOption} onChange={this.handleSelect} />
                    <label htmlFor="email">Email:</label>
                    <input type='text' id="email" onChange={this.handleChange}/>
                    <label htmlFor="password">Password:</label>
                    <input type='password' id="password" onChange={this.handleChange}/>
                    <label htmlFor="confirm_password">Confirm password:</label>
                    <input type='password' id="confirm_password" onChange={this.handleChange}/>
                    
                    <center><button id='signupButton' type="submit" disabled>Sign up</button></center>
                    
                </form>
            </div>
        )
    }
}