import React from 'react';
import '../index.css';
import GenQR from './QRcode';


const Home = () => {

    const userID = localStorage.getItem('QSuserID');

    if(userID && userID!==false){
        console.log("User logged in");
        return(
            <div className="container">
                <center><h2>Welcome to Quick-Shop</h2></center>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Dui faucibus in ornare quam viverra orci sagittis. Nunc sed velit dignissim sodales ut. Quisque egestas diam in arcu cursus euismod quis. Convallis posuere morbi leo urna molestie at elementum eu. Ultrices gravida dictum fusce ut placerat. Aliquam etiam erat velit scelerisque in dictum non consectetur. Eget dolor morbi non arcu. Purus in massa tempor nec feugiat nisl pretium. Quisque sagittis purus sit amet volutpat consequat mauris nunc congue.</p>
                <GenQR />
            </div>
        )
    }
    else{
        console.log("User not logged in. Please Signup or Signin.")
        return(
            <div className="container">
            <center><h3><a href="/signup"><span className='bButton'>Sign up</span></a></h3> <h5>Or</h5> <h3><a href="/login"><span className='bButton'>Sign in</span></a></h3></center>
            </div>
        )
    }

}

export default Home